.grid {
  display: grid;
  grid-template-columns: repeat(9, 50px);
  gap: 2px;
  margin: auto;
  justify-content: center;
}

/* Base cell */
.grid-cell {
  position: relative;                 /* for ::after overlay */
  width: 50px;
  height: 50px;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
}

/* Filled cells on the board */
.grid-cell.filled {
  background-color: #4CAF50;
}

/* Hover/preview overlay (added/colored via extra classes) */
.grid-cell.highlighted::after {
  content: "";
  position: absolute;
  inset: 0;
  pointer-events: none;
  border-radius: 2px;
  z-index: 2;
}

/* Valid drop = green tint + outline */
.grid-cell.highlighted.preview-ok::after {
  background: rgba(76, 175, 80, 0.22);
  outline: 2px solid #4CAF50;
  outline-offset: -2px;
}

/* Invalid drop = red tint + outline */
.grid-cell.highlighted.preview-bad::after {
  background: rgba(211, 47, 47, 0.16);
  outline: 2px solid #d32f2f;
  outline-offset: -2px;
}

/* Drag source niceties */
.shape-draggable,
.shape-cell {
  touch-action: none;
  user-select: none;
}
.shape-draggable { cursor: grab; }
.shape-draggable:active { cursor: grabbing; }

.shape-cell.filled { cursor: grab; }
.shape-cell.filled:active { cursor: grabbing; }
